import React from 'react'
import {  Link } from "react-router-dom";

const Mycart = ({cartItems,handleRemoveItem,handleUpdateQty}) => {
    
  return (  
    <div>
       <div
          className="invisible fixed bottom-0 top-0 right-0 z-[1045] flex w-[80vw] md:w-[50vw] lg:w-[30vw]  translate-x-full flex-col border-none bg-white bg-clip-padding text-neutral-700 shadow-sm outline-none transition duration-300 ease-in-out dark:bg-neutral-800 dark:text-neutral-200 [&[data-te-offcanvas-show]]:transform-none"
          tabIndex="-1"
          id="offcanvasRight"
          aria-labelledby="offcanvasRightLabel"
          data-te-offcanvas-init>
          <div className="flex items-center justify-between p-4">
            <h5
              className="mb-0 font-semibold leading-normal"
              id="offcanvasRightLabel">
              MY CART
            </h5>
            <button
              type="button"
              className="box-content rounded-none border-none opacity-50 hover:no-underline hover:opacity-75 focus:opacity-100 focus:shadow-none focus:outline-none"
              data-te-offcanvas-dismiss>
              <span
                className="w-[1em] focus:opacity-100 disabled:pointer-events-none disabled:select-none disabled:opacity-25 [&.disabled]:pointer-events-none [&.disabled]:select-none [&.disabled]:opacity-25">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24"
                  strokeWidth="1.5"
                  stroke="currentColor"
                  className="h-6 w-6">
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    d="M6 18L18 6M6 6l12 12" />
                </svg>
              </span>
            </button>
          </div>

          <div className="pointer-events-auto w-full h-[70vh]">
          <div className="flex h-full flex-col overflow-y-scroll bg-white ">
            <div className="flex-1 overflow-y-auto py-6 px-4 sm:px-6">
              <div className="mt-8">
                <div className="flow-root">
                  <ul role="list" className="-my-6 divide-y divide-gray-200">
                    {cartItems && cartItems.line_items.map((item)=>{
                    return  <li className="flex py-6">
                      <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                        <img src={`${item.image.url}`} alt="Front of satchel with blue canvas body, black straps and handle, drawstring top, and front zipper pouch." className="h-full w-full object-cover object-center"/>
                      </div>

                      <div className="ml-4 flex flex-1 flex-col">
                        <div>
                          <div className="flex justify-between text-base font-medium text-gray-900">
                            <h3>
                              <a href="#">{item.name}</a>
                            </h3>
                            <p className="ml-4">{item.line_total.formatted_with_symbol}</p>
                          </div>
                          <p className="flex mt-1 text-sm text-gray-500">Price:{item.price.formatted_with_symbol}</p>
                        </div>
                        <div className="flex flex-1 items-end justify-between text-sm">
                            <div className='flex items-center'>
                                <button onClick={()=>{handleUpdateQty(item.id,item.quantity-1)}} className='bg-gray-300 hover:bg-gray-400 active:bg-gray-300 mx-2 h-8 font-bold text-lg p-2 rounded'>-</button>
                                <p className="text-black font-bold text-xl mx-2">{item.quantity}</p>
                                <button onClick={()=>{handleUpdateQty(item.id,item.quantity+1)}} className='bg-gray-300 hover:bg-gray-400 active:bg-gray-300 mx-2 h-8 font-bold text-lg p-2 rounded'>+</button>
                            </div>
                          <div className="flex">
                            <button onClick={()=>{handleRemoveItem(item.id)}} type="button" className="font-medium text-indigo-600 hover:text-indigo-500">Remove</button>
                          </div>
                        </div>
                      </div>
                            </li>
                    })
                    }
                  </ul>
                </div>
              </div>
            </div>

          </div>
        </div>
        <div className="border-t h-[20vh] border-gray-200 py-6 px-4 sm:px-6">
            <div className="flex justify-between text-base font-medium text-gray-900">
            <p>Subtotal</p>
            <p>{cartItems.subtotal.formatted_with_symbol}</p>
            </div>
            <div className="mt-6">
            <Link to="/checkout" className="flex items-center justify-center rounded-md border border-transparent bg-indigo-600 px-6 py-3 text-base font-medium text-white shadow-sm hover:bg-indigo-700">Checkout</Link>
            </div>
            <div className="mt-6 flex justify-center text-center text-sm text-gray-500">
            <p>
                or
                <Link to="/" type="button" className="mx-2 font-medium text-indigo-600 hover:text-indigo-500">
                Continue Shopping
                <span aria-hidden="true"> &rarr;</span>
                </Link>
            </p>
            </div>
        </div>

        </div>
    </div>
  )
}

export default Mycart
