import React, {useState,useEffect} from "react";
import Bag from "./Bag";
import Address from "./Address";
import Payment from "./Payment";
import { commerce } from "../lib/commerce";

const Checkout = ({cart,handleEmptyCart}) => {
 const stages=['Bag','Address','Payment'];
 const [tokenId, setTokenId] = useState(null);
 const [activeStage, setActiveStage] = useState(0)


 useEffect(() => {
    const generateToken = async () =>{
    const token = await commerce.checkout.generateToken(cart.id, { type: 'cart' });
    console.log("tokenid is ",token.id);
    setTokenId(token);
    };
    generateToken();
 }, []);

 const handleStageChange=(stageIndex) =>{
    setActiveStage(stageIndex);
 };

 let stageComponent;
 if(tokenId && activeStage === 0){
    stageComponent = <Bag handleStageChange={handleStageChange} cartItems={cart} handleEmptyCart={handleEmptyCart} />;
 }
 else if (tokenId && activeStage === 1){
    stageComponent = <Address handleStageChange={handleStageChange} cartItems={cart} checkoutTokenId={tokenId} />;
 }
 else if(tokenId && activeStage === 2){
    stageComponent = <Payment handleStageChange={handleStageChange} />;
 }

 return (
 <div>
    {stageComponent}
 </div>
 )
};

export default Checkout
