import React from 'react'
import {  Link  } from "react-router-dom";

const Address = ({handleStageChange}) => {
  return (
    <div>
      <div className="flex items-center justify-center shadow-xl h-[12vh] w-full">
        <nav aria-label="Page navigation example">
            <div className="flex">
                <div className="relative left-[-7rem] lg:left-[-35rem]  md:left-[-10rem]">
            <Link
        to="/">
        <img
          src="./myntra.webp"
          style={{height: "65px"}}
          alt="" />
      </Link>
      </div>
      <div>
            <ul className="list-style-none flex">
            <li>
                <a
                className="relative rounded bg-transparent py-1.5 px-3 hidden lg:block lg:text-lg text-neutral-600 transition-all duration-300 hover:bg-neutral-100 dark:text-white dark:hover:bg-neutral-700 dark:hover:text-white"
                href="#"
                >Bag</a
                >
            </li>
            <h1  className="hidden lg:block">..............</h1>
            <li aria-current="page">
                <a
                className="relative  rounded bg-transparent py-1.5 px-3 hidden lg:block lg:text-lg text-green-500  font-bold transition-all duration-300 hover:bg-neutral-100 dark:text-white dark:hover:bg-neutral-700 dark:hover:text-white"
                href="#"
                >Address</a
                >
            </li>
            <h1 className="hidden lg:block">..............</h1>
            <li>
                <a
                className="relativerounded bg-transparent py-1.5 px-3 hidden lg:block lg:text-lg text-neutral-600 transition-all duration-300 hover:bg-neutral-100 dark:text-white dark:hover:bg-neutral-700 dark:hover:text-white"
                href="#"
                >Payment</a
                >
            </li>
            </ul>
            </div>
            <div className="flex items-center relative right-[-10rem] lg:right-[-33.5rem] ">
                <img src="./secure.png" className="h-[3rem] w-[5rem]"/>
                <span>100% Secure</span>
            </div>
            </div>
        </nav>
        </div>
      <div className="mt-10 sm:mt-0 flex justify-center">
  <div className="md:grid md:grid-cols-3 md:gap-6 flex items-center mt-10">
    <div className="mt-5 md:col-span-2 md:mt-0">
      <form action="#" method="POST">
        <div className=" shadow sm:rounded-md sm:h-[80vh] w-[35vw] lg:h-[70vh]">
          <div className="bg-white px-4 py-5 sm:p-6">
            <div className="grid grid-cols-6 gap-6">
              <div className="col-span-6 sm:col-span-3">
                <label for="first-name" className="block text-sm font-medium leading-6 text-gray-900">First name</label>
                <input type="text" name="first-name" id="first-name" autocomplete="given-name" className="mt-2 block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"/>
              </div>

              <div className="col-span-6 sm:col-span-3">
                <label for="last-name" className="block text-sm font-medium leading-6 text-gray-900">Last name</label>
                <input type="text" name="last-name" id="last-name" autocomplete="family-name" className="mt-2 block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"/>
              </div>

              <div className="col-span-6 sm:col-span-4">
                <label for="email-address" className="block text-sm font-medium leading-6 text-gray-900">Email address</label>
                <input type="text" name="email-address" id="email-address" autocomplete="email" className="mt-2 block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"/>
              </div>

              <div className="col-span-6 sm:col-span-3">
                <label for="country" className="block text-sm font-medium leading-6 text-gray-900">Country</label>
                <select id="country" name="country" autocomplete="country-name" className="mt-2 block w-full rounded-md border-0 bg-white py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6">
                  <option>United States</option>
                  <option>Canada</option>
                  <option>Mexico</option>
                </select>
              </div>

              <div className="col-span-6">
                <label for="street-address" className="block text-sm font-medium leading-6 text-gray-900">Street address</label>
                <input type="text" name="street-address" id="street-address" autocomplete="street-address" className="mt-2 block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"/>
              </div>

              <div className="col-span-6 sm:col-span-6 lg:col-span-2">
                <label for="city" className="block text-sm font-medium leading-6 text-gray-900">City</label>
                <input type="text" name="city" id="city" autocomplete="address-level2" className="mt-2 block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"/>
              </div>

              <div className="col-span-6 sm:col-span-3 lg:col-span-2">
                <label for="region" className="block text-sm font-medium leading-6 text-gray-900">State / Province</label>
                <input type="text" name="region" id="region" autocomplete="address-level1" className="mt-2 block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"/>
              </div>

              <div className="col-span-6 sm:col-span-3 lg:col-span-2">
                <label for="postal-code" className="block text-sm font-medium leading-6 text-gray-900">ZIP / Postal code</label>
                <input type="text" name="postal-code" id="postal-code" autocomplete="postal-code" className="mt-2 block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"/>
              </div>
            </div>
          </div>
          <div className="bg-gray-50 px-4 py-3 text-right sm:px-6">
            <button type="submit" className="border border-danger-600 h-auto w-[30vw] font-bold bg-pink-600 text-white py-3" onClick={()=>{handleStageChange(2)}}>Add Address</button>
          </div>
        </div>
      </form>
    </div>
  </div>
</div>
<div>
<footer className="text-gray-600 body-font">
  <div className="bg-gray-100 ">
    <div className="container h-[10vh] flex flex-wrap flex-col sm:flex-row items-center justify-center">
      <div className="border border-gray-400 p-2 m-2">
        <img src="./Visa.webp" className="h-8 w-12 "/>
      </div>
      <div className="border border-gray-400 p-2 m-2">
        <img src="./mastercard.png" className="h-8 w-12 "/>
      </div>
      <div className="border border-gray-400 p-2 m-2">
        <img src="./Rupay.png" className="h-8 w-12 "/>
      </div>
      <div className="border border-gray-400 p-2 m-2">
        <img src="./paypal.png" className="h-8 w-12 "/>
      </div>
      <div className="border border-gray-400">
        <img src="./BHIM-UPI.jpg" className=" p-2 h-12 w-16"/>
      </div>
    </div>
  </div>
</footer>
</div>
  </div>
  )
}

export default Address
