import './App.css';
import Home from './components/Home';
import { commerce } from './lib/commerce';
import React, { useState, useEffect } from "react";
import Loading from './components/Loading';
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Navbar from './components/Navbar';
import Footer from './components/Footer';
import MenAllProducts from './components/MenAllProducts';
import MenBottomwear from './components/MenBottomwear';
import MenTopwear from './components/MenTopwear';
import WomenAllProducts from './components/WomenAllProducts';
import WomenBottomwear from './components/WomenBottomwear';
import WomenTopwear from './components/WomenTopwear';
import Checkout from './components/Checkout';



function App() {
  const [product, setProduct] = useState([])
  const [men, setMen] = useState([])
  const [menbottomwear, setMenbottomwear] = useState([])
  const [mentopwear, setMentopwear] = useState([])
  const [women, setWomen] = useState([])
  const [womenbottomwear, setWomenbottomwear] = useState([])
  const [womentopwear, setWomentopwear] = useState([])
  const [isLoading, setIsLoading] = useState(true)
  const [cart, setCart] = useState({})

const handleCart = async (productId,qty) =>{
  const item = await commerce.cart.add(productId,qty)
  setCart(item)
}
const handleUpdateQty = async (productId,quantity) =>{
  const item = await commerce.cart.update(productId,{quantity})
  console.log("updated")
  setCart(prevItem=>({...prevItem,item}))
}
const handleRemoveItem = async (productId) =>{
  const {item} = await commerce.cart.remove(productId)
  setCart(item)
}
const handleEmptyCart = async () =>{
  const {item} = await commerce.cart.empty()
  setCart(item)
}

  useEffect(() => {
    const fetchProducts = async () => {
      const { data } = await commerce.products.list();
      setProduct(data);
    };
    fetchProducts();
  
    const fetchMen = () => {
      const menProducts = product.filter((item) => {
        const categories = item.categories;
        return categories.some((categories) => categories.slug === "men");
      });
      setMen(menProducts);
    };
  
    const fetchMenbottomwear = () => {
      const menProducts = product.filter((item) => {
        const categories = item.categories;
        return categories.some(
          (categories) => categories.slug === "men-bottomwear"
        );
      });
      setMenbottomwear(menProducts);
    };
  
    const fetchMentopwear = () => {
      const menProducts = product.filter((item) => {
        const categories = item.categories;
        return categories.some(
          (categories) => categories.slug === "men-topwear"
        );
      });
      setMentopwear(menProducts);
    };
  
    const fetchWomen = () => {
      const womenProducts = product.filter((item) => {
        const categories = item.categories;
        return categories.some((categories) => categories.slug === "women");
      });
      setWomen(womenProducts);
    };
  
    const fetchWomenbottomwear = () => {
      const womenProducts = product.filter((item) => {
        const categories = item.categories;
        return categories.some(
          (categories) => categories.slug === "women-bottomwear"
        );
      });
      setWomenbottomwear(womenProducts);
    };
  
    const fetchWomentopwear = () => {
      const womenProducts = product.filter((item) => {
        const categories = item.categories;
        return categories.some(
          (categories) => categories.slug === "women-topwear"
        );
      });
      setWomentopwear(womenProducts);
    };

    const fetchCart = async () =>{
      const data = await commerce.cart.retrieve();
      setCart(data);
    }
    Promise.all([fetchMen(), fetchMenbottomwear(), fetchMentopwear(),fetchWomen(), fetchWomenbottomwear(), fetchWomentopwear(), fetchCart()]).then(
      () => {
        setIsLoading(false);
      }
    );
  }, [product]);


if(isLoading){
  return <Loading/>
}
  return (
    <div className="App">
    <BrowserRouter>
    <Navbar cartItems={cart} handleRemoveItem={handleRemoveItem} handleUpdateQty={handleUpdateQty}/>
    <Routes>
      <Route path="/" element={<Home/>} />
      <Route path="/Mens" element={<MenAllProducts onhandleCart={handleCart} products={men}/>}/>
      <Route path="/Mensbottomwear" element={<MenBottomwear onhandleCart={handleCart} products={menbottomwear}/>} />
      <Route path="/Menstopwear" element={<MenTopwear onhandleCart={handleCart} products={mentopwear}/>} />
      <Route path="/Womens" element={<WomenAllProducts onhandleCart={handleCart} products={women}/>} />
      <Route path="/Womenstopwear" element={<WomenTopwear onhandleCart={handleCart} products={womentopwear}/>} />
      <Route path="/Womensbottomwear" element={<WomenBottomwear onhandleCart={handleCart} products={womenbottomwear}/>} />
      <Route path="/checkout" element={<Checkout cart={cart} handleEmptyCart={handleEmptyCart}/>}/>
    </Routes>
    <Footer/>
    </BrowserRouter>
    </div>
    
  );
}

export default App;
